import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Calendar } from "primereact/calendar";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { InputNumber } from "primereact/inputnumber";
import { InputSwitch } from "primereact/inputswitch";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { currencyActions } from "../../store/currency/currencySlice";
import { glAccountActions } from "../../store/glAccount/glAccountSlice";
import { manufacturerActions } from "../../store/manufacturer/manufacturerSlice";
import { productActions } from "../../store/product/productSlice";
import { productCategoryActions } from "../../store/productCategory/productCategorySlice";
import { taxActions } from "../../store/tax/taxSlice";
import { usageUnitActions } from "../../store/usageUnit/usageUnitSlice";
import { userActions } from "../../store/user/userSlice";
import { vendorActions } from "../../store/vendor/vendorSlice";
import { IAppState } from "../../models/appState";
import { IProduct } from "../../models/product";
import { ITax } from "../../models/tax";
import { v4 as uuidv4 } from "uuid";
import {
  convertDateToString,
  convertStringToDate,
  convertCurrency,
} from "../../utilities/helpers";

const ProductEdit: React.FC = () => {
  const defaultItem: IProduct = {
    id: "",
    name: "",
    productNo: "",
    productCode: "", // Part Number
    productCategoryId: "",
    manufacturerId: "",
    qtyPerUnit: 0,
    unitPrice: 0,
    packSize: 0,
    costFactor: 0,
    commissionRate: 0,
    active: false,
    usageUnitId: "",
    reorderLevel: 0,
    website: "",
    manufacturerPartNo: "",
    vendorPartNo: "",
    serialNo: "",
    qtyInStock: 0,
    qtyInDemand: 0,
    glAccountId: "",
    vendorId: "",
    imagename: "",
    currencyId: "",
    purchaseCost: 0,
    expireDate: "",
    handlerId: "",
    description: "",
    taxes: [],
  };
  const navigateTo = "/products";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);

  const [
    product,
    isFetching,
    isError,
    currencies,
    glAccounts,
    manufacturers,
    products,
    productCategories,
    taxes,
    usageUnits,
    users,
    isProfile,
    profile,
    vendors,
  ] = useAppSelector(
    (state: IAppState) => [
      state.products.product,
      state.products.isFetching,
      state.products.isError,
      state.currencies.currencies,
      state.glAccounts.glAccounts,
      state.manufacturers.manufacturers,
      state.products.products,
      state.productCategories.productCategories,
      state.taxes.taxes,
      state.usageUnits.usageUnits,
      state.users.users,
      state.users.isProfile,
      state.users.profile,
      state.vendors.vendors,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(currencyActions.getCurrencies());
      dispatch(glAccountActions.getGLAccounts());
      dispatch(manufacturerActions.getManufacturers());
      dispatch(productCategoryActions.getProductCategories());
      dispatch(taxActions.getTaxes());
      dispatch(usageUnitActions.getUsageUnits());
      dispatch(userActions.getUsers());
      dispatch(vendorActions.getVendors());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(productActions.getProductById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode !== "edit" &&
      isProfile &&
      profile &&
      profile.currencyId &&
      item.currencyId === "" &&
      profile.currencyId !== item.currencyId
    ) {
      setItem({ ...item, currencyId: profile.currencyId });
    }
  }, [params, isProfile, profile, item]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== product?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(product);
    }
  }, [params, item, product, isFetching, isError]);

  useEffect(() => {
    const items = [...(item.taxes || [])];
    if (!dirty && (taxes || []).length > 0 && items.length === 0) {
      taxes.forEach((x) => {
        if (x.status) {
          items.push({ ...x });
        }
      });
      if (items.length > 0) {
        setItem({ ...item, taxes: items });
      }
    }
  }, [dirty, item, taxes]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (
          products.find((x) => x.name?.toLowerCase() === name?.toLowerCase())
        ) {
          result = false;
        }
      } else {
        if (
          products.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, products]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    setValid(
      (item.name || "").trim().length > 0 &&
        isNameValid(item.name || "") &&
        (item.currencyId || "").trim().length > 0
    );
  }, [dirty, item, isNameValid]);

  const convertAmounts = (value: any) => {
    const id = item.currencyId ? item.currencyId : "";
    const unitPrice = convertCurrency(id, value, item.unitPrice, currencies);
    const purchaseCost = convertCurrency(
      id,
      value,
      item.purchaseCost,
      currencies
    );

    return {
      currencyId: value,
      unitPrice,
      purchaseCost,
    };
  };

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    if (tname === "expireDate") {
      setItem({
        ...item,
        [tname]: convertDateToString(tvalue),
      });
    } else if (tname === "currencyId") {
      setItem({ ...item, ...convertAmounts(tvalue) });
    } else {
      setItem({ ...item, [tname]: tvalue });
    }

    if (tname === "name") {
      const isValidName = isNameValid(tvalue);
      setErrors({
        ...errors,
        nameInvalid: !isValidName,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const copyTaxes = () => {
    const items: ITax[] = [];
    if ((item.taxes || []).length > 0) {
      item.taxes?.forEach((x) => {
        items.push({ ...x });
      });
    }

    return items;
  };

  const onTaxDefaultChange = (e: any) => {
    setDirty(true);
    const items = copyTaxes();
    if (items.length > 0) {
      const tax = items.find((x) => x.id === e.target.id);
      if (tax) {
        tax.default = e.target.value;
        if (tax.default) {
          const taxItem = taxes.find((x) => x.id === e.target.id);
          if (taxItem) {
            tax.value = taxItem.value;
          }
        }

        setItem({ ...item, taxes: items });
      }
    }
  };

  const onTaxValueChange = (e: any) => {
    setDirty(true);
    const items = copyTaxes();
    if (items.length > 0) {
      const tax = items.find((x) => x.id === e.target.id);
      if (tax) {
        tax.value = +e.target.value;
        setItem({ ...item, taxes: items });
      }
    }
  };

  const mapTaxes = () => {
    return item.taxes?.map((value: ITax) => (
      <div key={value.id} className="col-12">
        <div className="grid">
          <div className="col">
            <label htmlFor={value.id}>{value.name}</label>
          </div>
          <div className="col">
            <div className="p-fixed-switch">
              <InputSwitch
                id={value.id}
                name="default"
                checked={value.default}
                value="0"
                onChange={onTaxDefaultChange}
              />
            </div>
          </div>
          <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
          <div className="col">&nbsp;</div>
          <div className="col">
            <InputNumber
              id={value.id}
              name="value"
              value={value.value}
              disabled={value.default}
              min={0}
              onValueChange={onTaxValueChange}
            />
          </div>
        </div>
      </div>
    ));
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(productActions.updateProduct(item));
    setTimeout(() => {
      dispatch(productActions.getProducts());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Product: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Product</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="ProductName">
                  Product Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="ProductName"
                  name="name"
                  maxLength={100}
                  placeholder="Product Name"
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message severity="error" text="Product Name is required." />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message
                    severity="error"
                    text="Product Name already exists."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ProductActive">Product Active</label>
              </div>
              <div className="col">
                <div className="p-fixed-switch">
                  <InputSwitch
                    id="ProductActive"
                    name="active"
                    checked={item.active || false}
                    value="0"
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PartNumber">Part Number</label>
              </div>
              <div className="col">
                <InputText
                  id="PartNumber"
                  name="productCode"
                  maxLength={40}
                  value={item.productCode}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Manufacturer">Manufacturer</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Manufacturer"
                  name="manufacturerId"
                  options={manufacturers}
                  optionLabel="name"
                  optionValue="id"
                  value={item.manufacturerId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="ProductCategory">Product Category</label>
              </div>
              <div className="col">
                <Dropdown
                  id="ProductCategory"
                  name="productCategoryId"
                  options={productCategories}
                  optionLabel="name"
                  optionValue="id"
                  value={item.productCategoryId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="VendorName">Vendor Name</label>
              </div>
              <div className="col">
                <Dropdown
                  id="VendorName"
                  name="vendorId"
                  options={vendors}
                  optionLabel="name"
                  optionValue="id"
                  value={item.vendorId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Website">Website</label>
              </div>
              <div className="col">
                <InputText
                  id="Website"
                  name="website"
                  maxLength={255}
                  value={item.website}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ExpireDate">Expire Date</label>
              </div>
              <div className="col">
                <Calendar
                  id="ExpireDate"
                  name="expireDate"
                  value={convertStringToDate(item.expireDate)}
                  onChange={onChange}
                  showButtonBar
                  showIcon
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="VendorPartNo">Vendor Part No</label>
              </div>
              <div className="col">
                <InputText
                  id="VendorPartNo"
                  name="vendorPartNo"
                  maxLength={200}
                  value={item.vendorPartNo}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ManufacturerPartNo">Manufacturer Part No</label>
              </div>
              <div className="col">
                <InputText
                  id="ManufacturerPartNo"
                  name="manufacturerPartNo"
                  maxLength={200}
                  value={item.manufacturerPartNo}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="SerialNo">Serial No</label>
              </div>
              <div className="col">
                <InputText
                  id="SerialNo"
                  name="serialNo"
                  maxLength={200}
                  value={item.serialNo}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="GLAccount">GL Account</label>
              </div>
              <div className="col">
                <Dropdown
                  id="GLAccount"
                  name="glAccountId"
                  options={glAccounts}
                  optionLabel="name"
                  optionValue="id"
                  value={item.glAccountId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Pricing Information</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Currency">
                  Currency <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Dropdown
                  id="Currency"
                  name="currencyId"
                  options={currencies}
                  optionLabel="name"
                  optionValue="id"
                  value={item.currencyId}
                  onChange={onChange}
                  className="p-combobox"
                />
                {(errors as any)?.currencyId && (
                  <Message severity="error" text="Currency is required." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="UnitPrice">Unit Price</label>
              </div>
              <div className="col">
                <InputNumber
                  id="UnitPrice"
                  name="unitPrice"
                  value={item.unitPrice}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="CommissionRate">Commission Rate (%)</label>
              </div>
              <div className="col">
                <InputNumber
                  id="CommissionRate"
                  name="commissionRate"
                  value={item.commissionRate}
                  onValueChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="PurchaseCost">Purchase Cost</label>
              </div>
              <div className="col">
                <InputNumber
                  id="PurchaseCost"
                  name="purchaseCost"
                  value={item.purchaseCost}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Stock Information</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="UsageUnit">Usage Unit</label>
              </div>
              <div className="col">
                <Dropdown
                  id="UsageUnit"
                  name="usageUnitId"
                  options={usageUnits}
                  optionLabel="name"
                  optionValue="id"
                  value={item.usageUnitId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="QtyUnit">Qty / Unit</label>
              </div>
              <div className="col">
                <InputNumber
                  id="QtyUnit"
                  name="qtyPerUnit"
                  value={item.qtyPerUnit}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="QtyStock">Qty. in Stock</label>
              </div>
              <div className="col">
                <InputNumber
                  id="QtyStock"
                  name="qtyInStock"
                  value={item.qtyInStock}
                  onValueChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ReoderLevel">Reoder Level</label>
              </div>
              <div className="col">
                <InputNumber
                  id="ReoderLevel"
                  name="reorderLevel"
                  value={item.reorderLevel}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Handler">Handler</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Handler"
                  name="handlerId"
                  options={users}
                  optionLabel="lastName"
                  optionValue="id"
                  value={item.handlerId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="QtyDemand">Qty. in Demand</label>
              </div>
              <div className="col">
                <InputNumber
                  id="QtyDemand"
                  name="qtyInDemand"
                  value={item.qtyInDemand}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Taxes</h3>
          </div>
        </div>
        <div className="grid">{mapTaxes()}</div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Description</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="description"
              value={item.description}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default ProductEdit;
